.contact {

    &__content {
        display: flex;

        @include screen(480) {
            flex-direction: column;
            max-height: 729px;
        }
    }

    &__form {
        max-width: 730px;
        flex-basis: 730px;
        flex-grow: 1;
    }

    &__info {
        flex-grow: 1;
        padding-right: 30px;
        font-size: 12px;
        line-height: 20px;

        @include screen(600) {
            padding-right: 14px;
        }
        @include screen(378) {
            padding-right: 7px;
        }


        a {
            font-size: 12px;
            color: $font-color;
            transition: .3s;

            &:hover {
                text-decoration: underline;
            }
        }

        &-item {
            position: relative;
            padding-left: 30px;

            @include screen(600) {
                padding-left:17px ;
            }
            @include screen(420) {
                padding-left:17px;
                padding-bottom: 10px;
            }


            & + .contact__info-item {
                margin-top: 28px;
            }

            img {
                position: absolute;
                left: 0;
                top: 0;
                max-width: 15px;
            }

            &:last-child {

                img {
                    top: 4px;
                }
            }
        }
    }
}